import { getDataLocalStorage } from '../api/localStorageFavcCocktails';
import { refs } from '../refs/refs';
import { markupSearchingCocktails } from '../templates/markupSearchingCocktails';
import { FAV_COCKTAILS } from '../utils/constans';
import {
  clearDomELements,
  onReject,
  renderElementsMarkup,
} from '../utils/utils';

export async function getFavorCoctails() {
  console.log(
    'Cocktails in localStorage : ',
    await getDataLocalStorage(FAV_COCKTAILS)
  );

  try {
    const result = await getDataLocalStorage(FAV_COCKTAILS);
    onResolve(result);
  } catch (error) {
    onReject(error);
  }
}

function onResolve(arrFavCoctails) {
  const { favoriteCocktailsList, favoriteCocktailSubTitle } = refs;

  if (arrFavCoctails.length === 0) {
    favoriteCocktailSubTitle.textContent =
      "You haven't added any favorite cocktails yet";
    clearDomELements(favoriteCocktailsList);
    return;
  }

  //   console.log(markupSearchingCocktails(arrFavCoctails));
  renderElementsMarkup(
    favoriteCocktailsList,
    markupSearchingCocktails,
    arrFavCoctails
  );
}
